import { ChangeEvent, Fragment, useCallback, useEffect, useState } from "react";
import { Helmet } from "react-helmet-async";
import { Link } from "react-router-dom";
import {
  Card,
  CardContent,
  CardMedia,
  IconButton,
  MenuItem,
  SvgIcon,
  Typography,
} from "@mui/material";
import { useForm } from "react-hook-form";
import { serialize } from "object-to-formdata";

import WASettingsApi from "Apis/WASettingsApi";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import FormFileField from "SharedComponents/FormComponents/FormFileField";
import { useGlobal } from "Contexts/App";
import { useSnacks } from "Contexts/Snack";
import {
  useWaBasicSettingsData,
  useWaBasicSettingsApi,
} from "Contexts/WhatsApp";
import { ActionArea, ActionButton } from "SharedComponents/CommonComponents";
import {
  BotActions,
  BotActionSX,
  BotActionsSX,
  BotCardSX,
  BotInfo,
  CardSX,
  CardContentSX,
  CardHeading,
  Content,
  ContentHeader,
  ContentInner,
  ContentForm,
  Field,
  FlexRow,
  FlexRowGapSX,
  FlexRowSX,
  FormSelectFieldSX,
  FormTextAreaFieldSX,
  FormTextFieldSX,
  GrowColumn,
  HalfColumn,
  HeaderTitle,
  ShrinkColumn,
  HalfColumnSX,
} from "Containers/BusinessSettings/LayoutComponents";
import { LeftArrow } from "Assets/Icons";

const WABasicSettings = () => {
  const { isMobile, isTablet, navigate } = useGlobal();
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { allowedVerticals, chatbotNumber } = useWaBasicSettingsData();
  const { setChatBotNumber } = useWaBasicSettingsApi();
  const [loading, setLoading] = useState<boolean>(true);
  const [updateBtnLabel, setUpdateBtnLabel] = useState<string>("Submit");
  const { control, handleSubmit, watch, reset, setValue } = useForm<any>({
    defaultValues: {
      address: "",
      description: "",
      email: "",
      file: undefined,
      profile_picture_url: "",
      websites: [],
      messaging_product: "",
      vertical: "",
    },
  });
  const formValues = watch();
  const { profile_picture_url, vertical } = formValues;
  const profilePictureChanged = ({
    target: { files },
  }: ChangeEvent<HTMLInputElement>) => {
    if (files && files.length > 0) {
      const [file] = files;
      setValue("file", file);
      setValue("profile_picture_url", URL.createObjectURL(file));
    }
  };
  const onSubmit = async (data: any) => {
    try {
      const formData: FormData = serialize(data);
      const res = await WASettingsApi.saveWABasicSettings(formData);
      if (res.errors) {
        throw Error(res.errors.pop().message);
      }
      if (!res.success && res.message) {
        throw Error(res.message);
      }
      setSuccessMessage(res.message);
      navigate!(0);
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };
  const fetchGeneralSettings = useCallback(async () => {
    try {
      const res = await WASettingsApi.getWABasicSettings();
      if (!res.success) {
        throw Error(res.message);
      }
      setUpdateBtnLabel("Update Business Information");
      reset(res.payload);
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      setLoading(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reset]);
  const fetchChatBots = useCallback(async () => {
    try {
      if (chatbotNumber === undefined) {
        const res = await WASettingsApi.getWAChatBots();
        if (!res.success) {
          throw Error(res.message);
        }
        const [firstChatBot] = res.payload;
        firstChatBot && setChatBotNumber(firstChatBot.id);
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      setLoading(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    fetchChatBots();
  }, [fetchChatBots]);

  useEffect(() => {
    fetchGeneralSettings();
  }, [fetchGeneralSettings]);

  return (
    <Fragment>
      <Helmet>
        <title>WhatsApp Basic Settings</title>
      </Helmet>
      <Content>
        {isMobile && (
          <ContentHeader>
            <IconButton
              component={Link}
              to="/business-settings"
              sx={{ width: 32, height: 32 }}
            >
              <LeftArrow />
            </IconButton>
            <HeaderTitle>Business Information</HeaderTitle>
          </ContentHeader>
        )}
        <ContentInner>
          <ContentForm onSubmit={handleSubmit(onSubmit)}>
            <Card elevation={0} sx={CardSX}>
              <CardContent sx={CardContentSX}>
                <CardHeading>Business Information</CardHeading>
                <FlexRow sx={FlexRowSX}>
                  <GrowColumn>
                    <Field sx={{ mb: 0, height: "100%" }}>
                      <FormTextField
                        control={control}
                        name="description"
                        label="Business Description"
                        rows={5}
                        sx={FormTextAreaFieldSX}
                        multiline
                      />
                    </Field>
                  </GrowColumn>
                  <ShrinkColumn>
                    <FormFileField
                      control={control}
                      name="file"
                      defaultValue={profile_picture_url}
                      onChange={profilePictureChanged}
                      previewAlt="Profile Image"
                      label="Upload Profile Image"
                    />
                  </ShrinkColumn>
                </FlexRow>
                <FlexRow sx={{ ...FlexRowSX, mt: 3.75 }}>
                  <GrowColumn>
                    <Field>
                      <FormTextField
                        select
                        control={control}
                        label="Business Vertical"
                        name="vertical"
                        sx={FormSelectFieldSX}
                        defaultValue={vertical ?? ""}
                      >
                        <MenuItem value={""} disabled>
                          {"Select Vertical"}
                        </MenuItem>
                        {allowedVerticals.map(
                          (
                            { label, value }: { label: string; value: string },
                            index: number
                          ) => (
                            <MenuItem key={index} value={value}>
                              {label}
                            </MenuItem>
                          )
                        )}
                      </FormTextField>
                    </Field>
                  </GrowColumn>
                </FlexRow>
                <FlexRow>
                  <GrowColumn>
                    <Field>
                      <FormTextField
                        control={control}
                        label="Business Address"
                        name="address"
                        sx={FormSelectFieldSX}
                      />
                    </Field>
                  </GrowColumn>
                </FlexRow>
                <FlexRow>
                  <GrowColumn>
                    <Field>
                      <FormTextField
                        control={control}
                        label="Email"
                        name="email"
                        sx={FormSelectFieldSX}
                      />
                    </Field>
                  </GrowColumn>
                </FlexRow>
                <FlexRow sx={FlexRowGapSX}>
                  <HalfColumn sx={HalfColumnSX}>
                    <Field>
                      <FormTextField
                        control={control}
                        name={"websites[0]"}
                        label="Website 1"
                        placeholder="Website 1"
                        sx={FormTextFieldSX}
                      />
                    </Field>
                  </HalfColumn>
                  <HalfColumn sx={HalfColumnSX}>
                    <Field>
                      <FormTextField
                        control={control}
                        name={"websites[1]"}
                        label="Website 2"
                        placeholder="Website 2"
                        sx={FormTextFieldSX}
                      />
                    </Field>
                  </HalfColumn>
                </FlexRow>
                <ActionArea>
                  {!loading && (
                    <ActionButton type="submit">{updateBtnLabel}</ActionButton>
                  )}
                </ActionArea>
              </CardContent>
            </Card>
            {chatbotNumber && (
              <Card elevation={0} sx={BotCardSX}>
                <CardMedia
                  component="img"
                  image="/static/images/svg/bot.svg"
                  alt="bot"
                  sx={{
                    display: "flex",
                    flexShrink: 1,
                    height: "auto",
                    maxWidth: "100%",
                    width: { xs: 194, md: "calc(100% - 200px)" },
                  }}
                />
                <BotInfo>
                  <Typography
                    component="h3"
                    sx={{
                      color: "#FFFFFF",
                      fontSize: 14,
                      fontWeight: 400,
                      lineHeight: "18px",
                    }}
                  >
                    {
                      "Start building your WhatsApp Chatbot in seconds. Use the advanced "
                    }
                    <strong>{"Chatflow"}</strong>
                    {
                      " tool by EasySocial, and build complex conversations in a jiffy."
                    }
                  </Typography>
                  <BotActions sx={BotActionsSX}>
                    <ActionButton
                      component={Link}
                      to={`/chat-flow/${chatbotNumber}`}
                      sx={BotActionSX}
                      disableIcon={true}
                    >
                      <span>{"Build Chatbot"}</span>
                      <SvgIcon
                        viewBox="0 0 14 10"
                        fill="none"
                        sx={{ width: 14, height: 10 }}
                      >
                        <path
                          fillRule="evenodd"
                          clipRule="evenodd"
                          d="M11.3692 4.19223L8.28352 1.00205L9.25275 0L14 4.90798L9.43077 10L8.46154 8.99796L11.3692 5.62372H0V4.19223H11.3692Z"
                          fill="#12B0EE"
                        />
                      </SvgIcon>
                    </ActionButton>
                    {isTablet && (
                      <CardMedia
                        component="img"
                        image="/static/images/svg/messages.svg"
                        alt="messages"
                        sx={{ width: "auto" }}
                      />
                    )}
                  </BotActions>
                </BotInfo>
              </Card>
            )}
          </ContentForm>
        </ContentInner>
      </Content>
    </Fragment>
  );
};

export default WABasicSettings;
